import * as yup from 'yup';

const formtaskcreateSchema = yup.object().shape({

  title: yup.string()
    .required('Campo obrigatrio')
    .min(4, 'Mnimo de 4 caracteres')
    .max(20, 'Mximo de 20 caracteres'),
  description: yup.string()
    .min(4, 'Mnimo de 4 caracteres')
    .max(50, 'Mximo de 50 caracteres'),
  duration: yup.number()
    .required('Campo obrigatrio'),
  cost: yup.number()
    .required('Campo obrigatrio'),
  status: yup.string(),
    // .required('Campo obrigatrio'),
  type: yup.string()
    .required('Campo obrigatrio'),
  profitable: yup.boolean()
    .required('Campo obrigatrio'),
  creationDate: yup.date()
    .required('Campo obrigatrio'),
  dueDate: yup.date()
    .required('Campo obrigatrio'),

});
export default formtaskcreateSchema;
