import * as yup from 'yup';

const formprojectcreateSchema = yup.object().shape({

  title: yup.string()
    .required('Campo obrigatrio')
    .min(4, 'Mnimo de 4 caracteres')
    .max(20, 'Mximo de 20 caracteres'),
  description: yup.string()
    .min(4, 'Mnimo de 4 caracteres')
    .max(50, 'Mximo de 50 caracteres'),
  hourPrice: yup.number()
    .required('Campo obrigatrio'),
  duration: yup.string()
    .required('Campo obrigatrio'),
  cost: yup.number()
    .required('Campo obrigatrio'),
  status: yup.string(), // .required('Campo obrigatrio'),
  creationDate: yup.date()
    .required('Campo obrigatrio'),
  dueDate: yup.date()
    .required('Campo obrigatrio'),
  // user: yup.string()
  //   .required('Campo obrigatrio'),
});
// type: mongoose.Types.ObjectId, ref: "User", required: true
export default formprojectcreateSchema;
