import * as yup from 'yup';

const formloginSchema = yup.object().shape({
  username: yup.string()
    .required('Campo obrigatrio')
    .min(4, 'Mnimo de 4 caracteres')
    .max(20, 'Mximo de 20 caracteres'),
  password: yup.string()
    .required('Campo obrigatrio')
    .min(5, 'Mnimo de 5 caracteres')
    .max(60, 'Mximo de 60 caracteres'),
});

export default formloginSchema;
