import * as yup from 'yup';

const formtaskeditSchema = yup.object().shape({

  title: yup.string()
    .min(4, 'Mnimo de 4 caracteres')
    .max(20, 'Mximo de 20 caracteres'),
  description: yup.string()
    .min(4, 'Mnimo de 4 caracteres')
    .max(50, 'Mximo de 50 caracteres'),
  duration: yup.number(),
  cost: yup.number(),
  status: yup.string(),
  type: yup.string(),
  profitable: yup.boolean(),
  creationDate: yup.date(),
  dueDate: yup.date(),

});
export default formtaskeditSchema;
