import * as yup from 'yup';

const formeditSchema = yup.object().shape({

  name: yup.string()
    .required('Campo obrigatrio')
    .min(4, 'Mnimo de 4 caracteres')
    .max(50, 'Mximo de 50 caracteres'),
  username: yup.string()
    .required('Campo obrigatrio')
    .min(4, 'Mnimo de 4 caracteres')
    .max(20, 'Mximo de 20 caracteres'),
  email: yup.string()
    .required('Campo obrigatrio')
    .email('Formato invlido'),
  currentPassword: yup.string()
    .required('Campo obrigatrio')
    .min(6, 'Mnimo de 6 caracteres')
    .max(60, 'Mximo de 60 caracteres'),
  newPassword: yup.string()
    .required('Campo obrigatrio')
    .min(6, 'Mnimo de 6 caracteres')
    .max(60, 'Mximo de 60 caracteres'),
  confirmationNewPassword: yup.string()
    .required('Campo obrigatrio')
    .min(6, 'Mnimo de 6 caracteres')
    .max(60, 'Mximo de 60 caracteres'),

});

export default formeditSchema;
